export default {
  data: function data() {
    return {
      tableHeight: 400,
      loading: false,
      pageSizes: [20, 50, 100],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_myTicketPage") ? Number(localStorage.getItem("c_myTicketPage")) : 20,
      total: 0,
      totalPage: 0,
      items: [],
      DialogEditComment: {
        loading: false,
        uploadLoading: false,
        visible: false,
        contents: "",
        shopName: "",
        attach: [],
        commentId: "",
        item: null
      },
      DialogEditCommentDefault: "{}",
      msgLoading: false,
      msgId: "",
      msgList: [],
      messageText: "",
      bottomUpload: "",
      msgImg: [],
      sendMsgLoading: false,
      bottomUploadLoading: false
    };
  },
  computed: {
    activeItem: function activeItem() {
      var _this = this;

      var obj = null;

      if (this.msgId) {
        this.items.some(function (item) {
          if (item.id == _this.msgId) {
            obj = item;
            return true;
          }
        });
      }

      return obj;
    }
  },
  mounted: function mounted() {
    var _this2 = this;

    this.setChatCardHeight();
    this.DialogEditCommentDefault = JSON.stringify(this.DialogEditComment);
    this.getItem(true);

    window.onresize = function () {
      _this2.setChatCardHeight();
    };

    var left = this.$refs.leftScroll.wrap;

    left.onscroll = function () {
      if (left.scrollTop + left.offsetHeight + 200 > left.scrollHeight) {
        if (_this2.totalPage > _this2.page) {
          _this2.page++;

          _this2.getItem();
        }
      }
    };
  },
  beforeDestroy: function beforeDestroy() {
    this.$refs.leftScroll.wrap.onscroll = function () {};

    window.onresize = function () {};
  },
  methods: {
    testHtpp: function testHtpp(msg) {
      if (!msg.contents) return;
      var textR = msg.contents; //				var reg = /(http:\/\/|https:\/\/)((\w|=|\?|\.|\/|&|-)+)/g;

      var reg = /(https?.*?)(?=http|$|<|>|\s|,)/gi;

      if (msg.type == 1) {
        textR = textR.replace(reg, "<a href='$1$2' target='_blank' style='color:#fff;'>$1$2</a>");
      } else {
        textR = textR.replace(reg, "<a href='$1$2' target='_blank' style='color:#303133;'>$1$2</a>");
      }

      return textR; //		let arr = [];let str="这是一个链接:https://www.baidu.com/ ";
      //			let reg = /http[s]?:\/\/.*?(\s)/gi;
      //			arr = str.match(reg);
      //			console.log(arr)
      //		let reg = /^((ht|f)tps?):\/\/([\w-]+(\.[\w-]+)*\/?)+(\?([\w\-\.,@?^=%&:\/~\+#]*)+)?$/;
      //    return reg.test(msg);
    },
    removeImg: function removeImg(i, arr) {
      arr.splice(i, 1);
    },
    bottomUploadimg: function bottomUploadimg(event) {
      var _this3 = this;

      this.bottomUploadLoading = true;
      this.$apiCall("api.Comment.uploadAttach", {
        "@file": event.target.files[0]
      }, function (r) {
        _this3.bottomUploadLoading = false;

        if (r.ErrorCode == "9999") {
          _this3.msgImg.push(r.Data.Results.url);
        } else {
          _this3.$elementMessage(r.Message, "error");
        }
      });
    },
    openBottomUpload: function openBottomUpload() {
      //底部上传图片触发
      document.getElementById("bottomUploadImg").click();
    },
    addMsg: function addMsg() {
      var _this4 = this;

      //发送聊天
      if (!this.messageText && !this.msgImg.length) {
        this.$elementMessage("The message cannot be empty", "error");
        return;
      }

      this.sendMsgLoading = true;
      this.$Burying({
        object: '11001'
      });
      this.$apiCall("api.Comment.addByUser", {
        commentId: this.msgId,
        contents: this.messageText,
        attach: this.msgImg
      }, function (r) {
        _this4.sendMsgLoading = false;

        if (r.ErrorCode == "9999") {
          _this4.messageText = "";
          _this4.msgImg = [];

          _this4.getItem(true);

          _this4.viewDetail(_this4.activeItem);

          _this4.$refs.leftScroll.wrap.scrollTop = 0;
        } else {
          _this4.$elementMessage(r.Message, "error");
        }
      });
    },
    setChatCardHeight: function setChatCardHeight() {
      // 聊天卡片高度
      this.$refs.chatCard.style.height = document.body.clientHeight - 190 + "px";
    },
    handleRemove: function handleRemove(file, fileList) {
      var _this5 = this;

      fileList.forEach(function (e, index) {
        if (e.name == file.name) {
          _this5.DialogEditComment.attach.splice(index, 1);
        }
      });
    },
    handleChange: function handleChange(file) {
      var _this6 = this;

      this.DialogEditComment.loading = true;
      this.DialogEditComment.uploadLoading = true;
      this.$apiCall("api.Comment.uploadAttach", {
        "@file": file.raw
      }, function (r) {
        _this6.DialogEditComment.loading = false;
        _this6.DialogEditComment.uploadLoading = false;

        if (r.ErrorCode == "9999") {
          _this6.DialogEditComment.attach.push(r.Data.Results.url);
        } else {
          _this6.$elementMessage(r.Message, "error");
        }
      });
    },
    viewDetail: function viewDetail(item, event) {
      var _this7 = this;

      if (event && event.target.localName == "img") {
        return;
      } // 查看详情


      this.msgLoading = true;
      this.$apiCall("api.Comment.getDetailByUser", {
        commentId: item.id
      }, function (r) {
        _this7.msgLoading = false;

        if (r.ErrorCode == "9999") {
          _this7.$root.$children[0].getMsgNum();

          _this7.$set(item, 'userRead', '1');

          _this7.msgList = [];
          r.Data.Results.forEach(function (e) {
            e.attach = JSON.parse(e.attach);

            _this7.msgList.unshift(e);
          });
          _this7.msgId = item.id;
          var el = document.getElementById("chatListBox");
          if (el) setTimeout(function () {
            el.scroll(0, el.scrollHeight);
          }, 500);
        } else {
          _this7.$elementMessage(r.Message, "error");
        }
      });
    },
    openAddComment: function openAddComment() {
      //打开新建工单
      if (this.$refs.uploadFile) this.$refs.uploadFile.clearFiles();
      this.DialogEditComment = JSON.parse(this.DialogEditCommentDefault);
      this.DialogEditComment.visible = true;
    },
    saveComment: function saveComment() {
      var _this8 = this;

      //保存新建工单
      if (!this.DialogEditComment.contents) {
        this.$message({
          message: "The question must not be empty",
          type: "error"
        });
        return false;
      }

      this.DialogEditComment.loading = true;
      this.$apiCall("api.Comment.addByUser", {
        commentId: this.DialogEditComment.commentId,
        contents: this.DialogEditComment.contents,
        shopName: this.DialogEditComment.shopName,
        attach: this.DialogEditComment.attach
      }, function (r) {
        _this8.DialogEditComment.loading = false;

        if (r.ErrorCode == 9999) {
          _this8.DialogEditComment.visible = false;

          _this8.$message({
            message: "Save Success",
            type: "success"
          });

          _this8.getItem(true);
        } else {
          _this8.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    getItem: function getItem(s) {
      var _this9 = this;

      //获取工单列表
      this.loading = true;

      if (s) {
        this.page = 1;
      }

      this.$apiCall("api.Comment.findByUser", {
        page: this.page,
        rowsPerPage: this.rowsPerPage
      }, function (r) {
        _this9.loading = false;

        if (r.ErrorCode == "9999") {
          if (s) {
            _this9.items = [];
          }

          r.Data.Results.forEach(function (e) {
            e.attach = JSON.parse(e.attach);

            _this9.items.push(e);
          });
          _this9.total = Number(r.Data.Pagination.totalCount);
          _this9.totalPage = Number(r.Data.Pagination.totalPage);
          if (!_this9.activeItem && _this9.page == 1) _this9.viewDetail(_this9.items[0]);
        } else {
          _this9.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this9.$userFailure();
          }
        }
      });
    }
  }
};